import axios from "axios";
export async function getLinkApi() {
  let response = await fetch("https://football--yungjabo.repl.co/get_data");
  const data = await response.json();
  return data;
  
}

export async function getVideoApi(content_id) {
  const url = `http://127.0.0.1:6878/ace/manifest.m3u8?format=json&id=${content_id}`;
  try {
    const response = await axios.get(url);
    console.log(response.data);
    return JSON.stringify(response.data.response.playback_url);
  } catch (error) {
    console.log("request failed:", error.message);
  }
}
