import { useEffect, useRef, useState } from "react";
import { getLinkApi, getVideoApi } from "./getLink";
import { Player, Hls } from "@vime/react";
import './main.scss'
export function Main() {
  const [links, setLinks] = useState([]);
  const [video, setVideo] = useState('');
  const hlsRef = useRef(null);
  useEffect(() => {
    async function getLink() {
      const links = await getLinkApi();
      console.log(links);
      setLinks(links);
    }
    getLink();
  }, []);

  async function startPlay(content_id) {
    let url = await getVideoApi(content_id);
    url = url.replace(/"/g, "");
    setVideo(url);
  }

  return (
    <div className="main">
      <div className="video">
        <Player controls>
          <Hls
            version="latest"
            config=""
            poster="/media/poster.png"
            preload="metadata"
          >
            <source data-src={video || ""} type="application/x-mpegURL" />
          </Hls>
          {/* ... */}
        </Player>
      </div>
      <ul className="links">
        {links.map((link,index) => (
          <li key={index}>
            <button onClick={() => startPlay(link)}>{index}</button>
          </li>
        ))}
      </ul>
    </div>
  );
}
